'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import supabase from '@/lib/supabaseBrowserClient'

export default function PostJobPage() {
  const router = useRouter()

  const [title, setTitle] = useState('')
  const [description, setDescription] = useState('')
  const [isPaid, setIsPaid] = useState(false)
  const [posterEmail, setPosterEmail] = useState('')
  const [message, setMessage] = useState('')
  const [loading, setLoading] = useState(false)

  // Fetch logged-in user’s email
  useEffect(() => {
    const getUser = async () => {
      const { data, error } = await supabase.auth.getUser()
      if (error || !data.user) {
        router.replace('/login') // not logged in
      } else {
        setPosterEmail(data.user.email!)
      }
    }

    getUser()
  }, [router])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setMessage('Posting your job...')

    const { error } = await supabase.from('jobs').insert([
      {
        title,
        description,
        is_paid: isPaid,
        poster_email: posterEmail,
      },
    ])

    if (error) {
      setMessage('❌ Failed to post job: ' + error.message)
    } else {
      setMessage('✅ Job posted! Redirecting...')
      setTimeout(() => router.replace('/'), 1000)
    }

    setLoading(false)
  }

  return (
    <div className="p-6 max-w-xl mx-auto text-center">
      <h1 className="text-2xl font-bold mb-6">Post a Job</h1>

      <form onSubmit={handleSubmit} className="space-y-4 text-left">
        <input
          type="text"
          placeholder="Job title"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          required
          className="w-full border px-3 py-2 rounded"
        />
        <textarea
          placeholder="Job description"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          className="w-full border px-3 py-2 rounded"
          rows={4}
        />
        <label className="flex items-center space-x-2">
          <input
            type="checkbox"
            checked={isPaid}
            onChange={() => setIsPaid(!isPaid)}
          />
          <span>This is a paid job</span>
        </label>

        <button
          type="submit"
          disabled={loading}
          className="bg-black text-white px-4 py-2 rounded w-full"
        >
          {loading ? 'Posting...' : 'Post Job'}
        </button>
      </form>

      {message && <p className="mt-4 text-sm text-gray-700 text-center">{message}</p>}
    </div>
  )
}
